@use '../GrowTogether/GrowTogether.scss';

.your-users {
  &__container {
    @extend .grow-together__container;
    @media (min-width: 1024px) {
      margin-bottom: 258px;
    }
  }
  &__inner-container {
    @extend .grow-together__inner-container;
  }
  &__background-image-container {
    @extend .grow-together__background-image-container;
  }
  &__background-image {
    &--top {
      background-image: url('../../assets/backgrounds/bg-section-top-mobile-2.svg');
      height: 103px;
      background-size: 100% 103px;
      @media (min-width: 1024px) {
        background-image: url('../../assets/backgrounds/bg-section-top-desktop-2.svg');
        height: 120px;
        background-size: 100% 120px;
      }
    }
    &--bottom {
      position: absolute;
      background-image: url('../../assets/backgrounds/bg-section-bottom-mobile-2.svg');
      height: 101px;
      width: 100%;
      background-size: 100% 101px;
      @media (min-width: 1024px) {
        background-image: url('../../assets/backgrounds/bg-section-bottom-desktop-2.svg');
        height: 120px;
        background-size: 100% 120px;
      }
    }
  }
  &__body {
    @extend .grow-together__body;
  }
  &__illustration-container {
    @extend .grow-together__illustration-container;
  }
  &__illustration {
    @extend .grow-together__illustration;
  }
  &__header {
    @extend .grow-together__header;
  }
  &__text {
    @extend .grow-together__text;
  }
}
