.contact {
  &__container {
    @media (min-width: 1200px) {
      max-width: 340px;
    }
  }
  &__logo {
    margin-bottom: 1rem;
    width: 200px;
    @media (min-width: 1200px) {
      max-width: 340px;
      margin-bottom: 28px;
    }
  }
  &__text {
    margin-bottom: 40px;
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    color: #ffffff;
    @media (min-width: 1200px) {
      margin-bottom: 24px;
    }
  }
  &__phone-email-container {
    margin-bottom: 1rem;
    display: flex;
    &--last {
      margin-bottom: 48px;
      @media (min-width: 1200px) {
        margin-bottom: 80px;
      }
    }
  }
  &__phone-email-icon {
    width: 24px;
    height: 24px;
    margin-right: 1rem;
  }
  &__phone-email {
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #ffffff;
    @media (min-width: 1200px) {
      font-size: 16px;
      line-height: 24px;
    }
  }
  &__social-media-icons {
    display: flex;
  }
  &__social-media-icon {
    height: 23px;
    width: auto;
    margin-right: 1rem;
    cursor: pointer;
    transition: filter 0.25s;
    filter: invert(100%) sepia(7%) saturate(0%) hue-rotate(188deg)
      brightness(105%) contrast(100%);
    &:hover {
      filter: invert(59%) sepia(66%) saturate(4799%) hue-rotate(161deg)
        brightness(95%) contrast(101%);
    }
    @media (min-width: 1200px) {
      height: 32px;
      margin-right: 24px;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
